import './App.css';
import React from 'react';
import { marked } from 'marked';
import {defaultText} from "./defaultText";

const prism = require("prismjs");

require("prismjs/components/prism-markup-templating");
require("prismjs/components/prism-css");
require("prismjs/components/prism-php");
require("prismjs/components/prism-json");
require("prismjs/components/prism-javascript");
require("prismjs/components/prism-jsx");
require("prismjs/components/prism-bash");
require("prismjs/components/prism-yaml");
require("prismjs/components/prism-toml");

marked.setOptions({
    breaks: true,
    highlight: (code, lang) => {
        if (prism.languages[lang]) {
            return prism.highlight(code, prism.languages[lang], lang);
        } else {
            return code;
        }
    },
})
class Main extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            markup: defaultText
        }
        this.handleChange = this.handleChange.bind(this)
        this.clear = this.clear.bind(this)
        this.reset = this.reset.bind(this)
    }
    handleChange(event) {
        this.setState({
            markup: event.target.value
        })
    }
    reset() {
        this.setState({
            markup: defaultText
        })
    }

    clear() {
        this.setState({
            markup: ''
        })
    }
    render() {
        return (
            <div className="container-fluid">
                <div className="row">
                    <div className="col-sm-6 p-3">
                        <div id='editor-container'>
                        <h2 className='sub-heading'><i className="bi bi-card-text"></i> Editor</h2>
                        <textarea id="editor" className='shadow-sm rounded' value={this.state.markup} onChange={this.handleChange} />
                        <div id='button-bar'>
                            <button className='btn btn-dark' onClick={this.reset}>Reset Demo <i className="bi bi-arrow-repeat"></i></button><button className='btn btn-dark' onClick={this.clear}>Clear Editor<i
                            className="bi bi-x"></i></button>
                            <button type='button' className='btn btn-dark' data-bs-toggle="modal" data-bs-target='#infoModal'><i className="bi bi-info-circle"></i></button>
                        </div>
                        </div>

                    </div>
                    <div className="col-sm-6 p-3">
                        <div id='preview-container'>
                        <h2 className='sub-heading'><i className="bi bi-caret-right-fill"></i> Preview </h2>
                        <div id="preview" className='shadow-sm rounded' dangerouslySetInnerHTML={{__html: marked(this.state.markup)}}></div>
                        </div>
                    </div>

                </div>
            </div>
        )
    }

}

function App() {
    return (
        <div className="App">
            <header className="App-header">
                <h2>
                    <i className="bi bi-markdown-fill"></i> Markdown Previewer
                </h2>
            </header>
            <Main/>
            <footer></footer>
        </div>
    );
}

export default App;
